import Vue from 'vue'
import uploader from 'vue-simple-uploader'
import App from './App'
import router from './router'
import './common/stylus/index.styl'
Vue.use(uploader)
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



